const Random = require('mockjs').Random
const total = 30
/** 
 *  id 活动id
 *  img 活动图片
 *  title 标题
 *  location 地点
 *  time 时间
 *  price 价格
 *  status 状态
 */

const post = Array(total).fill().map((item, idx) => {
  return {
    id: idx + 1,
    img: Random.dataImage('300x180'),
    title: Random.ctitle(15, 50),
    location: Random.city(true),
    time: Random.datetime('MM-dd')+' 星期一',
    price: '￥'+Random.integer(0,300)+'起',
    status: Random.integer(0, 1),
  }
})

let activitydata = {
  1: post
}

export default activitydata


