const Random = require('mockjs').Random
const total = 30
/** 
 *  uid 文章id
 *  title 标题
 *  author 作者
 *  time 创建时间
 *  reading 阅读量
 *  good 点赞数
 */

const post1 = Array(total).fill().map((item, idx) => {
  let reading = Random.integer(1, 99999) 
  return {
    uid: idx + 1,
    img: Random.dataImage('315x200'),
    title: Random.ctitle(15, 30),
    author: Random.cname(),
    time: Random.datetime('yyyy-MM-dd'),
    reading: Random.integer(reading/50, reading/10),
    good: Random.integer(reading/10, reading/5),
  }
})
