/****************************************************************************************/
// mockdata/index.js

var Mock = require('mockjs')

import _data from './articledata'
import _data2 from './activitydata'

/**
 *  @request query
 *  type 请求数据类型（1,2,3,4,5,6），默认为1
 *  page 请求页数，默认为1
 *  pageSize 每页数据条数，默认为10
 * 
 * @repsonse data
 *  type 请求数据类型（1,2,3,4,5,6），默认为1
 *  page 请求页数，默认为1
 *  pageSize 每页数据条数，默认为10
 *  data 表格数据
 *  total 数据总数
 *  
 */

function mockData(_query) {
    let query = _query || {}
    let id = query.id;
    let page = query.page || 1
    let pageSize = query.pagesize || 10
    let total = _data[id].length
    let data = _data[id].filter((item, index) => {
        return (index >= (page-1)*pageSize) && (index <= page*pageSize-1)
    })
    return {
        id: id,
        page: page,
        total: total,
        pagesize: pageSize,
        data: data
    }
}

function mockData2(_query) {
    let query = _query || {}
    let id = query.id;
    let page = query.page || 1
    let pageSize = query.pagesize || 10
    let total = _data2[id].length
    let data = _data2[id].filter((item, index) => {
        return (index >= (page-1)*pageSize) && (index <= page*pageSize-1)
    })
    return {
        id: id,
        page: page,
        total: total,
        pagesize: pageSize,
        data: data
    }
}

// Mock.mock( url, post/get , 返回的数据)
/**
 * options: {
 *  url: 请求地址,
 *  type: 请求方式get/post
 *  body: 请求参数,注意类型是字符串
 *  }
 */
Mock.mock('/api/list', 'get', (options) => {
    // 这里的options.body类型为字符串，传入的时候要转化一下，将其转成json格式。这里我偷懒，就没写了。。。
    return mockData(JSON.parse(options.body))
});

Mock.mock('/api/list2', 'get', (options) => {
    // 这里的options.body类型为字符串，传入的时候要转化一下，将其转成json格式。这里我偷懒，就没写了。。。
    return mockData2(JSON.parse(options.body))
});



Mock.setup({
    timeout: '200-600'
})


